  margin: 0%;
  padding: 0%;
}

body {
  width: 100vw;
  background-color: hsl(0, 0%, 4%);
  color: hsl(0, 0%, 98%);
  font-family: "Montserrat", sans-serif;
  text-align: center;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 0px;
}

h1 {
  font-size: 11rem;
  font-weight: 300;
}

h2 {
  font-size: 4.75rem;
  font-weight: 100;
}

.hero-section {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 6.62rem;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

#hero-video {
  position: absolute;
  z-index: -1;
}

#black-overlay,
#black_gradient-overlay {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}

#black-overlay {
  background-color: hsl(0, 0%, 4%);
  opacity: 85%;
}

#black_gradient-overlay {
  background: -webkit-linear-gradient(90deg, hsl(0, 0%, 4%), hsla(0, 0%, 88%, 0));
}

#hero-display {
  max-width: 55rem;
  background: -webkit-linear-gradient(240deg, hsl(0, 0%, 88%), hsla(0, 0%, 88%, 0));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#hero-sub_display {
  max-width: 55rem;
  background: -webkit-linear-gradient(60deg, hsl(0, 0%, 88%), hsla(0, 0%, 88%, 0));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main_cta,
.download_cta {
  overflow: hidden;
  position: relative;
  border: 1px solid;
  border-radius: 160px;
  width: 10rem;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main_cta:active,
.download_cta:active {
  background-color: hsla(0, 0%, 88%, 0.5);
  transition: 2s;
}

.main_cta > *, .download_cta > * {
  position: absolute;
  bottom: 2.7rem;
}

.arrow-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.gallery-section {
  width: 100vw;
}

.navbar {
  position: sticky;
  z-index: 1;
  top: 0;
  width: 100vw;
  height: 3.375rem;
  background-color: hsl(0, 0%, 15%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-padding {
  padding: 7.5rem 7.5rem;
}

.gallery-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.img-wrapper {
  display: flex;
  width: 100%;
  aspect-ratio: 16/9;
  border-radius: 30px;
  border: 3px solid hsl(0, 0%, 6%);
}

.img_prop-wrapper {
  position: relative;
  margin: 0rem auto;
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5rem;
  align-self: end;
}

.download_cta-wrapper {
  display: none;
  position: absolute;
  top: -13rem;
  padding-bottom: 3rem;
}

.load_more-btn {
  background-color: hsl(0, 0%, 15%);
  border: none;
  border-radius: 16px;
  color: hsl(0, 0%, 98%);
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  font-weight: 100;
  text-align: center;
  padding-top: 1.7rem;
  padding-bottom: 1.7rem;
  transition: 2s;
}
.load_more-btn:hover {
  background-color: hsl(0, 0%, 21%);
}
.load_more-btn:active {
  background-color: hsl(0, 0%, 15%);
  transition: 0.5s;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.scss"],"names":[],"mappings":"AA2BA;EACE,UAAA;EACA,WAAA;AA1BF;;AA6BA;EACE,YAAA;EACA,gCAlCW;EAmCX,sBA9BW;EA+BX,qCAAA;EACA,kBAAA;EACA,kBAAA;AA1BF;AA4BE;EACE,UAAA;AA1BJ;;AA8BA;EACE,gBAAA;EACA,gBAAA;AA3BF;;AA8BA;EACE,kBAAA;EACA,gBAAA;AA3BF;;AA8BA;EACE,kBAAA;EACA,gBAAA;EACA,aAAA;EACA,sBAAA;EACA,YAAA;EACA,aAAA;EACA,YAAA;EAxCA,aAAA;EACA,uBAAA;EACA,mBAAA;AAcF;;AA4BA;EACE,kBAAA;EACA,WAAA;AAzBF;;AA2BA;;EAEE,kBAAA;EACA,WAAA;EACA,WAAA;EACA,YAAA;AAxBF;;AA2BA;EACE,gCA/EW;EAgFX,YAAA;AAxBF;;AA2BA;EA3EE,+EAAA;AAoDF;;AA2BA;EACE,gBAAA;EA7EA,iFAAA;EACA,6BAAA;EACA,oCAAA;AAsDF;;AAwBA;EACE,gBAAA;EA5EA,gFAAA;EACA,6BAAA;EACA,oCAAA;AAwDF;;AAsBA;;EAEE,gBAAA;EACF,kBAAA;EACE,iBAAA;EACA,oBAAA;EACA,YAAA;EACA,aAAA;EAjFA,aAAA;EACA,uBAAA;EACA,mBAAA;AA+DF;AAmBE;;EACE,uCAAA;EACA,cAAA;AAhBJ;;AAoBA;EACE,kBAAA;EACA,cAAA;AAjBF;;AAoBA;EACE,aAAA;EACA,sBAAA;EACA,SAAA;AAjBF;;AAoBA;EACE,YAAA;AAjBF;;AAoBA;EACE,gBAAA;EACA,UAAA;EACA,MAAA;EACA,YAAA;EACA,gBAAA;EACA,iCAnImB;EAoBnB,aAAA;EACA,uBAAA;EACA,mBAAA;AA+FF;;AAkBA;EACE,sBAAA;AAfF;;AAkBA;EACE,aAAA;EACA,sBAAA;EACA,SAAA;AAfF;;AAkBA;EACE,aAAA;EACA,WAAA;EACA,kBAAA;EACA,mBAAA;EACA,gCAAA;AAfF;;AAkBA;EACE,kBAAA;EACA,iBAAA;EACA,aAAA;EAxIA,aAAA;EACA,uBAAA;EACA,mBAAA;EAwIA,sBAAA;EACA,SAAA;EACA,eAAA;AAbF;;AAgBA;EACE,aAAA;EACA,kBAAA;EACA,WAAA;EACA,oBAAA;AAbF;;AAiBA;EACE,iCA5KmB;EA6KnB,YAAA;EACA,mBAAA;EACA,sBA5KW;EA6KX,qCAAA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,mBAAA;EACA,sBAAA;EACA,cAAA;AAdF;AAgBE;EACE,iCAxLe;AA0KnB;AAgBE;EACE,iCA5LiB;EA6LjB,gBAAA;AAdJ","sourcesContent":["$blackColor: hsla(0, 0%, 4%, 1);\n$secondaryBlackColor: hsla(0, 0%, 6%, 1);\n$tertiaryBlackColor: hsla(0, 0%, 15%, 1);\n$fourthBlackColor: hsla(0, 0%, 21%, 1);\n\n$whiteColor: hsla(0, 0%, 98%, 1);\n\n@mixin heroLinearGradient {\n  background: -webkit-linear-gradient(90deg, $blackColor, hsla(0, 0%, 88%, 0));\n}\n@mixin displayLinearGradient {\n  background: -webkit-linear-gradient(240deg, hsla(0, 0%, 88%, 1), hsla(0, 0%, 88%, 0));\n  -webkit-backg